body
  margin: 0
  padding: 0
  box-sizing: border-box
  height: 100vh
  width: 100vw
  display: flex
  flex-direction: column
  align-items: center
  font-family: 'Roboto', sans-serif
  max-width: 100%
.filterInput
  margin: 20px
  font-size: 24px
  padding: 3px
  border: 1px solid black
  border-radius: 5px
.pageButton
  background-color: white
  font-size: 14px
  margin: 5px
  border: 1px solid black
  border-radius: 10px
  padding: 5px
.table
  margin: 15px 0
  font-size: 9px
  tr
    cursor: pointer
    &:nth-child(even)
      background-color: lightgrey
.customModal
  background-color: grey
  position: absolute
  width: 100vw
  height: 100vh
  top: 50%
  left: 50%
  font-size: 16px
  transform: translate(-50%, -50%)
  display: flex
  justify-content: center
  align-items: center
  flex-direction: column
.modalClosingBtn
  background-color: white
  font-size: 14px
  margin: 5px
  border: 1px solid black
  border-radius: 10px
  padding: 5px
.currentPage
  background-color: lightgray

@media (min-width: 360px)
  .table
    font-size: 10px

@media (min-width: 768px)
  .filterInput
    font-size: 36px
    padding: 10px
  .pageButton
    font-size: 26px
    padding: 10px
  .table
    font-size: 20px
  .customModal
    font-size: 26px
  .modalClosingBtn
    font-size: 26px
@media (min-width: 1024px)
  .pageButton
    font-size: 30px
    padding: 12px
    min-width: 60px
  .table
    font-size: 30px
  .customModal
    font-size: 40px
    width: 60vw
    height: 80vh
    border-radius: 15px
  .modalClosingBtn
    font-size: 40px
    